import React, { useState } from "react";
import mydata from "../data";
import "./menu.css";
import { Link } from "react-router-dom";
const Menu = () => {
  const [showlink, setShowLink] = useState(false);
  const [navbar, setnavbar] = useState(false);
  const changeBackground = () => {
    if (window.scrollY > 50) setnavbar(true);
    else setnavbar(false);
  };

  window.addEventListener("scroll", changeBackground);
  return (
    <div className={navbar ? "navbar active" : "navbar"}>
      <div className="logo"><i class="fab fa-battle-net"></i><i class="fab fa-angrycreative"></i></div>
      <div
        className="menubar"
        style={{ transform: showlink ? "translateX(0px)" : "" }}>
        {mydata.map((res) => (
          <li>
            <Link to={res.link}>{res.item}</Link>
          </li>
        ))}
      </div>
      <div className="hamburger">
        <i
          onClick={() => setShowLink(!showlink)}
          className={showlink ? "fas fa-times" : "fas fa-bars"}></i>
      </div>
    </div>
  );
};
export default Menu;
